  margin-block: var(--space-s);
}
:root .vs-m-medium {
  margin-block: var(--space-m);
}
:root .vs-m-large {
  margin-block: var(--space-l);
}
:root .vs-m-xl {
  margin-block: var(--space-2xl);
}
:root .gap-small {
  gap: var(--space-xs);
}
:root .gap-medium {
  gap: var(--space-m);
