/* 
FONTS
Self-hosted font details.
*/
@font-face {
  font-family: "Merriweather";
  font-weight: 400;
  font-style: normal;
  src: url("fonts/Merriweather-Regular.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Merriweather";
  font-weight: 700;
  font-style: normal;
  src: url("fonts/Merriweather-Bold.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Mona Sans";
  font-weight: 400;
  font-style: normal;
  src: url("fonts/MonaSans-Regular.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Mona Sans";
  font-weight: 400;
  font-style: italic;
  src: url("fonts/MonaSans-Italic.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Mona Sans";
  font-weight: 600;
  font-style: normal;
  src: url("fonts/MonaSans-Semi-Bold.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Mona Sans";
  font-weight: 600;
  font-style: italic;
  src: url("fonts/MonaSans-Semi-Bold-Italic.woff2") format("woff2");
  font-display: swap;
}
/* 
GLOBAL STYLES
Site-wide styles.
*/
html {
  scroll-behavior: smooth;
}

body {
  font-family: var(--font-family-body);
  font-size: var(--step-1);
  font-weight: var(--fw-light);
  line-height: 1.6;
  color: var(--clr-text-400);
  background: var(--clr-surface-700);
  overflow-x: hidden;
}
body .bg-image-fade {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  overflow: hidden;
  z-index: -2;
  transform: translateZ(0);
}
body .bg-image-fade > img {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  will-change: opacity;
  transition: opacity 0.1s ease;
}

main {
  margin-block-end: var(--space-3xl);
}

* {
  scrollbar-color: var(--clr-brand-primary) var(--clr-surface-400);
}
*::-webkit-scrollbar {
  width: 12px;
}
*::-webkit-scrollbar-track {
  background: var(--clr-surface-400);
}
*::-webkit-scrollbar-thumb {
  background: var(--clr-brand-primary);
  border-radius: 6px;
  border: 3px solid var(--clr-surface-400);
}

*::selection {
  background: var(--clr-brand-accent);
  color: var(--clr-surface-700);
  -webkit-text-stroke-width: 0;
}

/* 
REGION UTILITY
Consistent block padding for page sections.
*/
.region-small {
  padding-block: var(--region-space-small, var(--space-m-l));
}

.region-normal {
  padding-block: var(--region-space-normal, var(--space-xl-2xl));
}

.region-large {
  padding-block: var(--region-space, var(--space-2xl-3xl));
}

.region-bottom {
  padding-block-end: var(--region-space, var(--space-xl-2xl));
}

/* 
FLOW UTILITY
Spacing value between content blocks.
*/
:where(.flow-tiny, .single-post-article__container .post-content ul, .single-post-article__container .post-content ol) > :where(* + *) {
  margin-block-start: var(--flow-space-tiny, 0.5em);
}

:where(.flow-small) > :where(* + *) {
  margin-block-start: var(--flow-space-small, 0.5em);
}

:where(.flow) > :where(* + *) {
  margin-block-start: var(--flow-space, 1em);
}

:where(.flow-large) > :where(* + *) {
  margin-block-start: var(--flow-space-large, 2em);
}

:where(.flow-page-regions) > :where(* + *) {
  margin-block-start: var(--flow-space-xl, 8em);
}

/* 
WRAPPER UTILITY
Settings for the overall content width.
*/
.wrapper {
  position: relative;
  max-width: 90rem;
  margin-inline: auto;
  padding-inline: var(--gutter);
}
@media (min-width: 37.5rem) {
  .wrapper {
    padding-inline: var(--gutter-large);
  }
}

/* 
VISUALLY HIDEN UTILITY
Modern way of properly hiding elements.
*/
.visually-hidden {
  border: 0;
  clip: rect(0 0 0 0);
  height: 0;
  margin: 0;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
  white-space: nowrap;
}

/* 
BORDER RADIUS UTILITY
Values for consistent border radius.
*/
:root {
  --radius-xs: 6px;
  --radius-small: 8px;
  --radius-normal: 16px;
  --radius-large: 48px;
  --radius-circle: 9999px ;
}

.rounded-small {
  border-radius: var(--radius-small);
}

.rounded {
  border-radius: var(--radius-normal);
}

.rounded-xs {
  border-radius: var(--radius-xs);
}

/* 
ICON TEXT GROUPS UTILITY
Groupings for text that have either leading or ttailing icons.
*/
:is(p, h1, h2, h3, h4, h5, h6).icon-text-group {
  display: flex;
  align-items: center;
  gap: var(--space-2xs);
  margin-block-start: var(--space-3xs);
}
:is(p, h1, h2, h3, h4, h5, h6).icon-text-group svg {
  fill: currentColor;
}

/* 
BG BLUR UTILITY
Applies background colour and blur to an element.
** Remeber to add position: relative to the element.**
*/
.bg-blur::before {
  content: "";
  position: absolute;
  inset: 0;
  width: 100%;
  backdrop-filter: blur(10px);
  background-color: rgba(0, 0, 0, 0.2);
  z-index: -1;
}

/* 
VARIOUS FLEX LAYOUT CLASSES
*/
.flex {
  display: flex;
  align-items: center;
  gap: var(--space-s);
}

.flex-splitter {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.flex-push-right {
  justify-content: flex-end;
}

/* 
Image state styles
Fancy css filter effects for image link hovers.
*/
.img-hover {
  filter: saturate(1) brightness(1);
  transition: filter 0.2s ease-in-out;
}
.img-hover:hover {
  filter: saturate(1.1) brightness(1.15);
}

/* 
TYPOGRAPHY
Font styling for headings, paragraphs etc.
*/
:root {
  --font-family-headings: "Merriweather";
  --font-family-body: "Mona Sans";
  --fw-light: 300;
  --fw-reg: 400;
  --fw-semibold: 600;
  --fw-bold: 700;
}

h1, h2, h3, h4, h5 {
  font-family: var(--font-family-headings);
  font-weight: var(--fw-semibold);
}

h1 {
  font-size: var(--step-6);
  line-height: 1.3;
}

h2 {
  font-size: var(--step-5);
  line-height: 1.4;
}

h3 {
  font-size: var(--step-4);
  line-height: 1.4;
}

h4 {
  font-size: var(--step-3);
  line-height: 1.5;
}

h5 {
  font-size: var(--step-1);
  line-height: 1.6;
}

h6 {
  font-size: calc(var(--step--1) + 0.15rem);
  line-height: 1.4;
}

/* Improve line wrapping */
h1, h2, h3, h4, h5, h6 {
  text-wrap: balance;
}

p {
  text-wrap: pretty;
}

.section-title, .section-title-small {
  position: relative;
  display: flex;
  align-items: center;
  color: var(--clr-text-400);
  margin-block-end: var(--space-l);
  padding-inline-start: 72px;
}
.section-title::before, .section-title-small::before {
  content: "";
  position: absolute;
  left: 0;
  width: 56px;
  height: 56px;
  background: url("./images/logos/BBB-Icon.svg") no-repeat center center;
  background-color: var(--clr-surface-400);
  border-radius: var(--radius-small);
}

.section-title-small {
  margin-block-end: var(--space-m);
  padding-inline-start: 0;
}
.section-title-small::before {
  border: 0;
  clip: rect(0 0 0 0);
  height: 0;
  margin: 0;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
  white-space: nowrap;
}

.balance {
  text-wrap: balance;
}

.clr-100 {
  color: var(--clr-text-100);
}

.clr-400 {
  color: var(--clr-text-400);
}

.clr-900 {
  color: var(--clr-text-900);
}

.bold {
  font-weight: var(--fw-bold);
}

.body-small, .small {
  font-size: var(--step-0);
}

.body-large, .large {
  font-size: var(--step-2);
}

.semi-bold {
  font-weight: var(--fw-semibold);
}

a {
  color: var(--clr-text-100);
  text-decoration-color: transparent;
  text-underline-offset: 4px;
}
a:hover, a:active {
  text-decoration: underline;
  text-decoration-color: var(--clr-text-link);
}

:focus-visible {
  outline: 2px solid var(--clr-brand-accent);
  outline-offset: 0.25rem;
  border-radius: 2px;
}

a.link-alt,
body:is(.single-post) :is(.post-content) a {
  color: var(--clr-text-link);
  text-decoration: underline;
}
a.link-alt:hover, a.link-alt:active,
body:is(.single-post) :is(.post-content) a:hover,
body:is(.single-post) :is(.post-content) a:active {
  color: var(--clr-text-link-hover);
  text-decoration: underline;
  text-decoration-color: var(--clr-text-link-hover);
}

/* 
FORMS
Styling for all form elements.
*/
form input, form textarea {
  color: var(--clr-text-400);
  padding: var(--space-xs) var(--space-s);
  background: var(--clr-surface-400);
  border: 1px solid var(--clr-border-darker);
  border-radius: var(--radius-small);
}
y form input::placeholder, y form textarea::placeholder {
  font-size: 1.2rem;
  color: var(--clr-surface-100);
  opacity: 1;
}
form button.button-ghost {
  border-radius: 5px;
}
form [type=search]::-webkit-search-decoration,
form [type=search]::-webkit-search-cancel-button {
  appearance: none;
}

/* 
HEADER BLOCK
Header styling for logo, nav etc.
*/
.site-head {
  position: relative;
  padding: var(--space-xs) 0;
  background: hsla(217, 50%, 7%, 0.5);
}
.site-head .header-backdrop {
  position: absolute;
  inset: 0;
  height: 200%;
  backdrop-filter: blur(6px);
  mask-image: linear-gradient(to bottom, black 0% 50%, transparent 50% 100%);
  z-index: -1;
}
.site-head__inner {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
}
.site-head__logo img {
  width: clamp(12.5rem, 5.593vw + 8.914rem, 15.625rem);
  height: auto;
}
.site-head__navigation {
  display: flex;
  flex-direction: column;
  gap: var(--space-2xs);
  align-items: end;
}
.site-head__navigation .nav {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: var(--space-l);
  justify-content: flex-end;
  font-weight: var(--fw-semibold);
  margin: 0;
}
@media (hover) {
  .site-head__navigation .nav > a {
    transition: opacity 0.2s ease;
  }
  .site-head__navigation .nav:hover > *:not(:hover) {
    opacity: 0.25;
    transition: opacity 0.2s ease;
  }
}
.site-head__navigation .nav .nav-search-icon svg {
  width: 16px;
  height: 16px;
  fill: var(--clr-text-100);
}
.site-head__navigation .nav .nav-search-icon svg:hover {
  fill: var(--clr-brand-primary);
}
.site-head__navigation .menu-nav-toggle {
  display: none;
}
@media (max-width: 45rem) {
  .site-head__navigation p {
    display: none;
  }
  .site-head .nav {
    position: fixed;
    width: 80%;
    height: 100vh;
    inset: 0 0 0 100%;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    padding: var(--space-3xl) var(--space-l);
    gap: var(--space-l);
    font-size: var(--step-1);
    background: var(--clr-surface-400);
    z-index: 1000;
    transform: translateX(0);
    transition: transform 0.3s cubic-bezier(0.77, 0, 0.18, 1);
  }
  .site-head .nav[data-visible=true] {
    transform: translateX(-100%);
  }
  .site-head .menu-nav-toggle {
    display: block;
    position: absolute;
    right: 0.35rem;
    width: 2.25rem;
    aspect-ratio: 1;
    background: none;
    border: none;
    transform: translateY(-50%);
    cursor: pointer;
    z-index: 9999;
  }
  .site-head .menu-nav-toggle::before {
    content: "";
    display: block;
    position: absolute;
    top: -20px;
    right: -10px;
    bottom: -20px;
    left: -30px;
  }
  .site-head .menu-nav-toggle span {
    position: absolute;
    top: 50%;
    display: block;
    width: 100%;
    height: 5px;
    background-color: var(--clr-text-100);
    transition: 0.25s ease-out;
  }
  .site-head .menu-nav-toggle span:nth-of-type(1) {
    top: 30%;
  }
  .site-head .menu-nav-toggle span:nth-of-type(2) {
    top: 70%;
  }
  .site-head .menu-nav-toggle[aria-expanded=true] {
    position: fixed;
    right: 2.6rem;
  }
  .site-head .menu-nav-toggle[aria-expanded=true] span {
    background-color: var(--clr-brand-secondary);
  }
  .site-head .menu-nav-toggle[aria-expanded=true] span:nth-of-type(1) {
    top: 50%;
    transform: rotate(45deg);
  }
  .site-head .menu-nav-toggle[aria-expanded=true] span:nth-of-type(2) {
    top: 50%;
    transform: rotate(-45deg);
  }
}
@media (max-width: 45rem) and (max-width: 599px) {
  .site-head .menu-nav-toggle[aria-expanded=true] {
    right: 2rem;
  }
}

/* 
PROSE BLOCK
Default value for spacing between text paragraphs.
*/
.prose {
  --flow-space: var(--space-m);
}

/* 
LATEST PODCAST EPISODE BLOCK
*/
.latest-podcast-ep {
  display: grid;
  grid-template-columns: 1fr;
  gap: var(--space-m);
  margin-block-start: var(--space-2xl);
  margin-block-end: var(--space-m);
}
@media (min-width: 67rem) {
  .latest-podcast-ep {
    grid-template-columns: 1fr 1fr;
  }
}
.latest-podcast-ep__content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: var(--space-s);
  margin-block-start: -6px;
}
.latest-podcast-ep a {
  align-self: flex-start;
}
.latest-podcast-ep a .post-thumb-img {
  height: 100%;
}
.latest-podcast-ep__details {
  display: flex;
  flex-direction: column;
  gap: var(--space-xs);
}
.latest-podcast-ep__details h4 {
  max-inline-size: 30ch;
}
.latest-podcast-ep__copy {
  display: flex;
  flex-direction: column;
  gap: var(--space-xs);
}
@media (max-width: 930px) {
  .latest-podcast-ep__excerpt {
    display: none;
  }
}

.app-links {
  position: relative;
  width: fit-content;
}

/* 
BUTTON STYLES
Styles for each button type.
*/
a.button,
button.button {
  position: relative;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: fit-content;
  min-width: 10ch;
  min-height: 44px;
  padding: calc(var(--space-2xs) + 0.2rem) var(--space-s);
  font-size: calc(var(--step--1) + 0.2rem);
  font-weight: var(--fw-bold);
  text-align: center;
  line-height: 1.1;
  color: var(--clr-text-100);
  background: linear-gradient(142deg, #4C95FA 2%, #1674F9 65%);
  border-radius: var(--radius-xs);
  overflow: hidden;
  z-index: 1;
}
@media (max-width: 40rem) {
  a.button,
  button.button {
    width: 100%;
  }
}
a.button::before,
button.button::before {
  content: "";
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(140deg, #4C95FA 2%, #1674F9 65%) padding-box, linear-gradient(170deg, #B4D3FD -30%, transparent 60%) border-box;
  border: 1px solid transparent;
  border-radius: var(--radius-xs);
  transition: all 0.1s ease-out;
  z-index: -1;
}
a.button:hover::before, a.button:active::before,
button.button:hover::before,
button.button:active::before {
  filter: brightness(0.9);
}
a.button:focus-visible,
button.button:focus-visible {
  outline: 3px solid var(--clr-brand-accent);
  outline-offset: 0.25rem;
  border-radius: 2px;
}

a.button {
  text-decoration: none;
}

button.button {
  border: none;
  background-color: transparent;
  font-family: inherit;
  padding: 0;
  cursor: pointer;
}

/* Ghost buttons */
.button-ghost, a.button-ghost {
  position: relative;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  min-width: 6ch;
  font-size: calc(var(--step--1) + 0.2rem);
  font-weight: var(--fw-bold);
  color: var(--clr-text-100);
  text-align: center;
  padding: var(--space-2xs) var(--space-s);
  background-color: transparent;
  border: 1px solid var(--clr-border-link);
  border-radius: var(--radius-small);
  text-decoration: none;
  transition: all 0.1s ease-out;
  cursor: pointer;
}
.button-ghost:hover, a.button-ghost:hover {
  background: rgba(109, 153, 248, 0.45);
}

/* 
BLOCKQUOTE
Layout and styling for in-post blockquotes.
*/
blockquote {
  position: relative;
  font-size: var(--step-3);
  font-style: italic;
  line-height: 1.4;
  margin-block: var(--space-2xl);
  padding-inline-start: var(--space-m);
}
blockquote::before {
  content: "“";
  position: absolute;
  width: 100%;
  top: -2rem;
  left: -1.1rem;
  font-size: 12rem;
  line-height: 1;
  color: var(--clr-brand-accent);
  opacity: 0.25;
}
blockquote p:not(:last-child) {
  font-weight: var(--fw-semibold);
  margin-block-end: var(--space-2xs);
  background: linear-gradient(90deg, hsl(334, 88%, 68%), hsl(215, 95%, 55%));
  background-clip: text; /* Ensures the gradient is clipped to the text */
  -webkit-text-fill-color: transparent; /* Makes the text itself transparent */
}
blockquote p:last-child {
  display: block;
  font-size: var(--step-1);
  color: var(--clr-text-900);
}

/* 
LINKS TO PODCAST APPS BLOCK
*/
.podcast-app-links {
  display: flex;
  flex-direction: column;
  gap: var(--space-xs);
}
.podcast-app-links__buttons {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  gap: var(--space-xs);
}
.podcast-app-links__buttons .button-ghost {
  padding: 0.4em 0.8em;
}
.podcast-app-links__buttons .link-column a {
  display: flex;
  min-height: 45px;
}
@media (max-width: 622px) {
  .podcast-app-links__buttons .link-column {
    flex: 45%;
  }
  .podcast-app-links__buttons a {
    flex-grow: 1;
  }
}
@media (max-width: 400px) {
  .podcast-app-links__buttons {
    flex-direction: column;
    flex-wrap: nowrap;
    width: 100%;
  }
}
.podcast-app-links .pod-links-menu details summary {
  list-style: none;
  cursor: pointer;
}
.podcast-app-links .pod-links-menu details summary::-webkit-details-marker {
  display: none;
}
.podcast-app-links .pod-links-menu details[open] summary {
  background: rgba(109, 153, 248, 0.4);
  border-radius: var(--radius-small);
}
.podcast-app-links .pod-links-menu .other-pod-links-popup {
  --popover-spacing: calc(var(--space-xs) - 0.3rem);
  position: absolute;
  bottom: 130%;
  right: 0;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: var(--popover-spacing);
  padding: var(--popover-spacing);
  background: var(--clr-surface-400);
  transition: all 0.2s ease-out;
  z-index: 100;
}
.podcast-app-links .pod-links-menu .other-pod-links-popup__item img {
  width: 3rem;
  filter: grayscale(1) brightness(0.9);
  transition: filter 0.2s ease-out;
}
.podcast-app-links .pod-links-menu .other-pod-links-popup__item img:hover {
  filter: grayscale(0) brightness(1);
}
@media (max-width: 622px) {
  .podcast-app-links .pod-links-menu .other-pod-links-popup {
    bottom: 3.5rem;
  }
}

/* 
POST PREVIEW CARDS
Layout and styling for articles post cards.
*/
article {
  position: relative;
  display: flex;
  flex-direction: column;
}
article a:has(img) {
  display: block;
}
article footer {
  display: flex;
  flex-direction: column;
  gap: var(--space-2xs);
  color: var(--clr-text-900);
}
article .post-card-content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: var(--space-2xs);
  margin-block-start: var(--space-xs);
}

article.post-card-alt .post-card-content {
  gap: var(--space-2xs);
}

/* 
AUTHOR META BLOCK
Author image and post meta styling.
*/
.author-meta {
  display: grid;
  grid-template-columns: 3rem 1fr;
  align-items: center;
  gap: var(--space-xs);
}
.author-meta__details {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  font-weight: var(--fw-semibold);
  color: var(--clr-text-900);
}
@media (max-width: 989px) {
  .author-meta__details {
    background: none;
  }
}

.post-card-alt .author-meta {
  padding-block-start: 0;
}
.post-card-alt .author-meta img {
  border: 0;
  clip: rect(0 0 0 0);
  height: 0;
  margin: 0;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
  white-space: nowrap;
}
.post-card-alt .author-meta__details {
  display: grid;
  grid-template-columns: max-content 1fr;
  align-items: center;
  gap: var(--space-2xs);
}
.post-card-alt .author-meta__details p {
  width: max-content;
}
.post-card-alt .author-meta__author-name::after {
  content: "•";
  color: inherit;
  padding-inline-start: var(--space-3xs);
}

/* 
CATEGORY TAG BLOCK
*/
.category-tag {
  display: inline-flex;
  align-items: center;
  gap: var(--space-3xs);
  width: fit-content;
  color: var(--clr-text-900);
}
.category-tag svg {
  fill: var(--clr-text-900);
}
.category-tag span {
  font-size: var(--step-0);
  font-weight: var(--fw-semibold);
  color: inherit;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  padding-block-start: 1px;
}

body:is(.single-post) .category-tag {
  color: var(--clr-brand-accent);
}
body:is(.single-post) .category-tag svg {
  fill: currentColor;
}

/* 
LATEST ARTICLES BLOCK
*/
.latest-articles-featured {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(min(100%, 20rem), 1fr));
  gap: var(--space-m);
}
@media (max-width: 720px) {
  .latest-articles-featured {
    gap: var(--space-xl);
  }
}
.latest-articles-featured .category-tag {
  border: 0;
  clip: rect(0 0 0 0);
  height: 0;
  margin: 0;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
  white-space: nowrap;
}

/* 
TESTIMONIALS STYLES
Styles for full-width and column based testimonials.
*/
.testimonial {
  width: 90%;
  margin-inline: auto;
  padding-block: var(--space-l);
}
@media (max-width: 768px) {
  .testimonial {
    width: 100%;
  }
}
.testimonial__content {
  position: relative;
  display: flex;
  flex-direction: column;
  gap: var(--space-xs);
  padding-inline-start: var(--space-s);
  margin-inline: auto;
  border-left: 4px solid var(--clr-brand-secondary);
}
.testimonial__content em {
  position: relative;
  color: var(--clr-surface-700);
  background: var(--clr-brand-secondary);
  padding: 0 3px 3px 3px;
  margin-inline-start: -4px;
}
.testimonial__content h4, .testimonial__content p {
  color: var(--clr-text-400);
}
.testimonial__content h4 {
  font-style: italic;
  font-weight: 400;
  text-wrap: wrap;
}
@media (max-width: 53em) {
  .testimonial__content {
    width: 100%;
  }
}
.testimonial__source {
  display: flex;
  flex-direction: column;
  gap: calc(var(--space-3xs) - 3px);
}
.testimonial__source h5, .testimonial__source cite {
  font-family: var(--font-family-body);
  font-style: normal;
}
.testimonial__source p {
  color: var(--clr-brand-secondary);
}
.testimonial__source cite {
  display: inline-flex;
  align-items: center;
  gap: var(--space-2xs);
  font-size: var(--step-0);
}
.testimonial__bg-image {
  position: absolute;
  top: 50%;
  transform: translateY(-55%);
  right: -3%;
  width: 250px;
  height: auto;
  opacity: 0.7;
  z-index: -1;
}
@media (max-width: 53em) {
  .testimonial__bg-image {
    display: none;
  }
}

.testimonials-multi-col {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(19rem, 1fr));
  gap: var(--space-xl);
}
.testimonials-multi-col .testimonial {
  width: 100%;
  padding-block: 0;
}
.testimonials-multi-col .testimonial__content {
  padding-inline-start: 0;
  border-left: none;
}
.testimonials-multi-col .testimonial__content h4 {
  font-size: var(--step-2);
}
.testimonials-multi-col .testimonial__bg-image {
  display: none;
}

/* 
BROWSE ALL BLOCK
Layout and styling for the browse all and side section.
*/
.browse-all {
  position: static;
  background-image: url("images/homepage-browse-all-bg.webp");
  background-repeat: no-repeat;
  background-size: contain;
}
@media (max-width: 989px) {
  .browse-all {
    background: none;
  }
}
.browse-all__container {
  display: flex;
  flex-wrap: wrap;
  gap: var(--space-2xl);
}
.browse-all__posts {
  flex-basis: 0;
  flex-grow: 999;
  min-inline-size: 50%;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(min(100%, 20rem), 1fr));
  gap: var(--space-m);
}
.browse-all__posts > :last-child {
  grid-column: 1/-1;
}
.browse-all .main-sidebar {
  flex-basis: 20.5rem;
  flex-grow: 1;
}
.browse-all article {
  margin-block-end: var(--space-s);
}

/* 
SIDEBAR
Layout and styling for the main sidebar that appears on the homepage and a select other pages.
*/
.main-sidebar .search-form {
  position: relative;
  display: grid;
}
.main-sidebar .search-form .search-field {
  width: 100%;
}
.main-sidebar .search-form button {
  position: absolute;
  right: var(--space-2xs);
  top: 50%;
  height: calc(100% - var(--space-xs));
  transform: translateY(-50%);
}
.main-sidebar .discord-link, .main-sidebar .intro {
  display: flex;
  flex-direction: column;
  gap: var(--space-s);
}
.main-sidebar .discord-link .intro, .main-sidebar .intro .intro {
  gap: var(--space-xs);
}
.main-sidebar .discord-link .intro img, .main-sidebar .intro .intro img {
  width: fit-content;
}
.main-sidebar .author-section-small__author-block {
  display: flex;
  gap: var(--space-xs);
  padding-block: var(--space-xs);
}
.main-sidebar .author-section-small__author-block img {
  border-radius: var(--radius-xs);
}
.main-sidebar .author-section-small__author-block .author-block-content {
  display: flex;
  flex-direction: column;
  gap: var(--space-3xs);
}
.main-sidebar .author-section-small__author-block .new-author-img {
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 56px;
  height: 56px;
  background-color: var(--clr-surface-400);
}
.main-sidebar .author-section-small__author-block p:not(:first-child) {
  color: var(--clr-text-900);
}
.main-sidebar .author-section-small__author-block:is(:first-child) {
  padding-block-start: 0;
}
.main-sidebar .author-section-small__author-block:is(:last-child) {
  padding-block-end: 0;
}
.main-sidebar .author-section-small__author-block:not(:last-child) {
  border-bottom: 1px solid var(--clr-border-darker);
}
@media (max-width: 621px) {
  .main-sidebar .sidebar-img {
    min-width: 100%;
    height: auto;
  }
}

/* 
SOCIAL CHANNELS
Layout and styling for the list of social channels.
*/
.social-channels li {
  border-block-end: 1px solid var(--clr-border-darker);
  padding-block: var(--space-xs);
}
.social-channels li:is(:first-child) {
  padding-block-start: 0;
}
.social-channels li:is(:last-child) {
  padding-block-end: 0;
  border: none;
}
.social-channels a {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.social-channels__item {
  display: flex;
  align-items: center;
  gap: var(--space-xs);
}
.social-channels__item img {
  width: 1.3rem;
}

/* 
CALL OUT PANEL
Layout and styling for panels that are styled differently to regular sections.
*/
.call-out-panel, .call-out-panel-large {
  position: relative;
  padding: calc(var(--space-s) + 0.25rem);
  background: var(--clr-surface-300);
  border-radius: var(--radius-small);
}
.call-out-panel::before, .call-out-panel-large::before {
  content: "";
  position: absolute;
  inset: 0;
  background: linear-gradient(to bottom right, var(--clr-surface-100), var(--clr-surface-300) 40%);
  margin: -1px;
  border-radius: 8px;
  z-index: -1;
}

.call-out-panel-large {
  padding: var(--space-3xl);
  border-radius: var(--radius-normal);
}
.call-out-panel-large::before {
  border-radius: var(--radius-normal);
}

/* 
REVIEW COMPENDIUM LINK FULL WIDTH SECTION
Styles for full-width section that links to the Reviews Compendium
*/
.review-compendium-link {
  position: relative;
  background-image: url("images/review-compendium-section-bg.webp");
  background-repeat: no-repeat;
  background-size: cover;
  overflow: hidden;
}
.review-compendium-link__content {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(26rem, 1fr));
  gap: var(--space-s);
}
.review-compendium-link__content .review-compendium-text {
  padding-block: var(--space-3xl);
}
.review-compendium-link__content .review-compendium-text a.button {
  width: fit-content;
  margin-block-start: var(--space-l);
}
.review-compendium-link__content img {
  max-width: 500px;
  height: auto;
  object-fit: cover;
  align-self: end;
  justify-self: end;
}
@media (max-width: 906px) {
  .review-compendium-link__content img {
    display: none;
  }
}

/* 
MULTI-COL POSTS
Layout and styling for small post cards in multiple columns (desktop).
*/
.more-articles {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(17rem, 1fr));
  gap: var(--space-2xl);
}
.more-articles ul {
  margin-block-end: var(--space-l);
}
.more-articles li {
  display: flex;
  gap: var(--space-s);
  padding-block: var(--space-m);
}
.more-articles li .more-articles-content a {
  display: block;
  margin-block-end: var(--space-2xs);
}
.more-articles li .more-articles-content a img {
  max-width: 142px;
  height: 80px;
  object-fit: cover;
}
.more-articles li .more-articles-content .post-meta {
  font-weight: var(--fw-semibold);
  color: var(--clr-text-900);
}
.more-articles li .more-articles-title {
  margin-block-start: -6px;
}
.more-articles li .more-articles-title h6 {
  font-size: var(--step-0);
}
.more-articles li:is(:first-child) {
  padding-block-start: 0;
}
.more-articles li:is(:last-child) {
  padding-block-end: 0;
}
.more-articles li:not(:last-child) {
  border-bottom: 1px solid var(--clr-border-darker);
}

/* 
ROUNDTABLE LINK PANEL
Styles for the Roundtable link panel
*/
.round-table-link-panel {
  position: relative;
  background-color: var(--clr-surface-600);
  overflow: hidden;
  z-index: 10;
}
.round-table-link-panel::before {
  content: "";
  position: absolute;
  inset: 1px;
  width: 100%;
  height: 110%;
  background-image: url("images/round-table-link-bg.webp");
  background-size: contain;
  background-position: right center;
  background-repeat: no-repeat;
  border-radius: var(--radius-normal);
  z-index: -1;
}
@media (max-width: 998px) {
  .round-table-link-panel::before {
    background-size: cover;
  }
}
.round-table-link-panel::after {
  content: "";
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(140deg, #09101B 100%, #09101B 100%) padding-box, linear-gradient(160deg, #363D49 -20%, transparent 40%) border-box;
  border: 1px solid transparent;
  border-radius: var(--radius-normal);
  z-index: -2;
}
.round-table-link-panel__content {
  position: relative;
  width: 100%;
  height: 100%;
  padding: var(--space-l) var(--space-l) var(--space-xl) var(--space-l);
  isolation: isolate;
}
.round-table-link-panel__content h4, .round-table-link-panel__content p {
  max-width: 55ch;
}
.round-table-link-panel__content p {
  margin-block-end: var(--space-s);
}
.round-table-link-panel__content::before {
  content: "";
  position: absolute;
  inset: 1px;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, rgb(9, 16, 27) 55%, rgba(9, 16, 27, 0) 100%);
  border-radius: var(--radius-normal);
  z-index: -2;
}
@media (max-width: 998px) {
  .round-table-link-panel__content::before {
    background: rgba(9, 16, 27, 0.85);
  }
}
.round-table-link-panel__content::after {
  content: "";
  position: absolute;
  bottom: -50px;
  left: -370px;
  width: 1200px;
  height: 200px;
  background: radial-gradient(circle, hsla(221, 91%, 70%, 0.15) 0, hsla(216, 50%, 7%, 0) 60%);
  border-radius: 50%;
  filter: blur(20px);
  z-index: -1;
}
@media (max-width: 998px) {
  .round-table-link-panel__content::after {
    border: 0;
    clip: rect(0 0 0 0);
    height: 0;
    margin: 0;
    overflow: hidden;
    padding: 0;
    position: absolute;
    width: 1px;
    white-space: nowrap;
  }
}

/* 
SEARCH OVERLAY BLOCK
Search overlay that appears when the search icon is clicked
*/
.search-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(1, 10, 29, 0.8);
  opacity: 0;
  will-change: opacity;
  pointer-events: none;
  transition: opacity 0.2s ease;
  z-index: 100;
}
.search-overlay__content {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 0;
  padding-inline: var(--gutter);
  background: rgba(1, 10, 29, 0.9);
  border-bottom: 1px solid var(--clr-border-darker);
  transition: height 0.2s ease;
  z-index: 150;
}
@media (min-width: 37.5rem) {
  .search-overlay__content {
    padding-inline: var(--gutter-large);
  }
}
.search-overlay__content .search-form {
  display: flex;
  flex-direction: column;
  gap: var(--space-s);
  width: 50rem;
  padding-block-end: var(--space-s);
  border-bottom: 2px solid var(--clr-border-darker);
}
.search-overlay__content .search-form .inputs {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.search-overlay__content .search-form .search-field {
  flex-grow: 1;
  font-size: 1.25rem;
  padding: 0;
  color: var(--clr-text-100);
  background: none;
  border: none;
  border-radius: 0;
}
.search-overlay__content .search-form .search-field:focus-visible {
  outline: none;
}
.search-overlay__content .search-form .search-field::placeholder {
  color: var(--clr-surface-200);
}
.search-overlay__content .search-form .search-submit {
  background: none;
  border: none;
  cursor: pointer;
}
.search-overlay__content .search-form .search-submit img {
  width: 1.5rem;
}

.search-overlay[aria-hidden=false] {
  opacity: 1;
  pointer-events: auto;
}

.search-overlay[aria-hidden=false] .search-overlay__content {
  height: 50vh;
}

/* 
POST TAGS
Layout and styling for single post page and search results tags.
*/
.post-tags {
  display: flex;
  align-items: center;
  gap: var(--space-2xs);
}
@media (max-width: 50rem) {
  .post-tags {
    flex-direction: column;
    flex-wrap: wrap;
    align-items: flex-start;
  }
}
.post-tags ul {
  display: flex;
  flex-wrap: wrap;
  gap: var(--space-2xs);
  list-style: none;
}
.post-tags ul li a {
  display: inline-block;
  width: max-content;
  font-size: var(--step-0);
  color: var(--clr-brand-secondary);
  padding: 0.188rem 0.75rem;
  border: 1px solid var(--clr-brand-secondary);
  border-radius: var(--radius-circle);
}
.post-tags ul li a:hover {
  text-decoration: none;
  background-color: var(--clr-brand-secondary);
  color: var(--clr-surface-700);
}

/* 
POST META
Layout and styling for mainly single post meta information.
*/
.post-meta {
  gap: var(--space-2xs);
}
@media (max-width: 23.063rem) {
  .post-meta {
    flex-direction: column;
    flex-wrap: wrap;
    align-items: flex-start;
    gap: var(--space-3xs);
  }
  .post-meta span {
    display: none;
  }
}

/* 
POST IMAGES
Layout and styling for featured and preview card images.
*/
article .post-thumb-img, article .singlepost-feat {
  width: 100%;
  height: auto;
  aspect-ratio: 16/9;
  object-fit: cover;
}
article .singlepost-feat {
  aspect-ratio: 16/8;
}
article figure {
  max-width: 100%;
  margin-inline: auto;
  padding-block-start: var(--space-l);
  margin-block-end: var(--space-xl);
}
article figure img {
  border-radius: var(--radius-small);
}
article figure figcaption {
  font-size: var(--step-0);
  font-style: italic;
  color: var(--clr-text-900);
  margin-block-start: var(--space-2xs);
}

/* 
POST CLOSING THOUGHTS
Layout and styling for the closing paragraph and review score.
*/
.closing-thoughts {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  gap: var(--space-xl-2xl);
  padding: var(--space-m);
  background: var(--clr-surface-300);
  overflow: hidden;
  z-index: 10;
}
.closing-thoughts__text {
  display: flex;
  flex-direction: column;
  gap: var(--space-xs);
  flex-basis: 0;
  flex-grow: 999;
  min-inline-size: 50%;
}
.closing-thoughts__text .closing-thoughts-title {
  font-size: var(--step-2);
  margin-block-start: 0;
}
.closing-thoughts__text p {
  color: var(--clr-text-900);
}
.closing-thoughts .score-wrapper {
  display: flex;
  position: relative;
  align-items: center;
  justify-content: center;
  flex-basis: 10rem;
  flex-grow: 1;
}
@media (max-width: 39.938rem) {
  .closing-thoughts .score-wrapper {
    flex-grow: unset;
  }
}
.closing-thoughts .score-wrapper .score-circle {
  width: 500px;
  height: auto;
  background: transparent; /* Ensure no white box behind SVG */
  transform: rotate(-90deg); /* Stroke starts from top */
}
@media (max-width: 39.938rem) {
  .closing-thoughts .score-wrapper .score-circle {
    max-width: 250px;
  }
}
.closing-thoughts .score-wrapper .score-circle-bg {
  fill: none;
  stroke: transparent; /* Adjust color as needed */
  stroke-width: 10;
}
.closing-thoughts .score-wrapper .score-circle-stroke {
  fill: none;
  stroke: var(--clr-brand-primary); /* Adjust color as needed */
  stroke-width: 6;
  stroke-dasharray: 314; /* Approx. circumference for r=50 */
  stroke-dashoffset: 314; /* Overridden inline by PHP */
  stroke-linecap: round; /* Rounded ends on the stroke */
}
.closing-thoughts .score-wrapper .score-value {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: var(--step-6);
  font-weight: var(--fw-bold);
  color: var(--clr-brand-primary);
  pointer-events: none;
}
.closing-thoughts::after {
  content: "";
  position: absolute;
  bottom: -150px;
  right: -450px;
  width: 1100px;
  height: 350px;
  background: radial-gradient(circle, hsla(215, 95%, 55%, 0.15) 0, hsla(215, 95%, 55%, 0) 60%);
  border-radius: 50%;
  filter: blur(20px);
  z-index: -1;
}
@media (max-width: 39.938rem) {
  .closing-thoughts::after {
    left: -450px;
  }
}

/* 
SITE FOOTER
Layout and styling for the site footer
*/
.footer {
  position: relative;
  padding-block-start: var(--space-xl);
  background: url("./images/footer-bg-glow-noise.webp") no-repeat bottom center;
  background-size: cover;
  overflow: hidden;
}
.footer::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 100%;
  height: 100%;
  background: url("./images/footer-tardis-bg.webp") no-repeat center;
  background-size: contain;
  z-index: 1;
}
@media (max-width: 768px) {
  .footer::before {
    background-position: bottom right;
    left: 30%;
    transform: none;
    background-size: 90%;
  }
}
.footer::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 100%;
  background: linear-gradient(to bottom, rgb(6, 12, 19) 5%, rgba(6, 12, 19, 0) 100%);
  pointer-events: none;
  z-index: 2;
}
.footer p {
  color: var(--clr-text-900);
}
.footer .footer-top {
  position: relative;
  display: grid;
  grid-template-columns: 1fr max-content max-content;
  gap: var(--space-xl);
  padding-block-end: var(--space-l);
  background: linear-gradient(to right, transparent, rgba(27, 47, 75, 0.6) 20%, rgba(27, 47, 75, 0.6) 80%, transparent);
  background-size: 100% 1px;
  background-position: 0 100%;
  background-repeat: no-repeat;
  z-index: 3;
}
@media (max-width: 768px) {
  .footer .footer-top {
    grid-template-columns: 1fr;
  }
}
.footer .footer-top__col1 {
  display: flex;
  flex-direction: column;
  gap: var(--space-s);
}
.footer .footer-top__col1 ul {
  display: flex;
  align-items: center;
  gap: var(--space-s);
}
.footer .footer-top__col1 ul li a img {
  transition: all 0.2s ease-out;
}
.footer .footer-top__col1 ul li a img:hover {
  transform: translateY(-3px);
}
.footer .footer-top ul.footer-list li:first-child {
  margin-block-end: var(--space-xs);
}
.footer .footer-top ul.footer-list li:not(:first-child) {
  font-size: var(--step-0);
}
.footer .footer-top ul.footer-list li a {
  color: var(--clr-text-900);
}
.footer .footer-bottom {
  position: relative;
  padding-block: var(--space-l);
  z-index: 3;
}
.footer .footer-bottom p {
  max-width: 75ch;
}

/* 
SINGLE POST STYLES
Layout and styling for single post pages.
*/
.single-post-article h1 {
  font-size: var(--step-7);
  line-height: 1.2;
  max-width: 28ch;
}
.single-post-article h2 {
  font-size: var(--step-3);
  line-height: 1.4;
  margin-block-start: var(--space-xl);
}
.single-post-article__header-meta {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  gap: var(--space-m);
  align-items: center;
  padding-block-start: var(--space-2xs);
  padding-block-end: var(--space-s);
}
@media (max-width: 50rem) {
  .single-post-article__header-meta {
    flex-direction: column;
    align-items: flex-start;
    gap: var(--space-s);
  }
}
.single-post-article__container {
  display: flex;
  flex-wrap: wrap;
  gap: var(--space-2xl);
  margin-block-start: var(--space-xl);
}
.single-post-article__container .article-body {
  flex-basis: 0;
  flex-grow: 999;
  min-inline-size: 63%;
}
.single-post-article__container .post-summary {
  display: flex;
  flex-direction: column;
  gap: var(--space-xs);
  padding: var(--space-l);
  padding-block-start: var(--space-m);
  background: var(--clr-surface-300);
}
.single-post-article__container .post-summary:before {
  background: linear-gradient(to bottom right, var(--clr-surface-200), var(--clr-surface-600) 50%);
}
.single-post-article__container .post-summary ul {
  display: flex;
  flex-direction: column;
  gap: var(--space-2xs);
  padding-inline-start: 1rem;
}
.single-post-article__container .post-summary ul li {
  list-style-type: disc;
  padding-inline-start: 0.25rem;
}
.single-post-article__container .post-summary ul li::marker {
  color: var(--clr-brand-accent);
}
.single-post-article__container .post-content p.lead {
  font-size: var(--step-2);
  font-weight: var(--fw-semibold);
  line-height: 1.5;
}
.single-post-article__container .post-content p.lead::first-letter {
  color: var(--clr-brand-accent);
  font-family: var(--font-family-headings);
  font-size: 6rem;
  line-height: 1;
  margin-block-start: -4px;
  margin-inline-end: var(--space-2xs);
  float: left;
}
.single-post-article__container .post-content ul, .single-post-article__container .post-content ol {
  margin-inline-start: 1.1rem;
}
.single-post-article__container .article-sidebar {
  flex-basis: 20rem;
  flex-grow: 1;
  border-inline-start: 1px solid var(--clr-border-darker);
  padding-inline-start: var(--space-2xl);
}
@media (max-width: 72.188rem) {
  .single-post-article__container .article-sidebar {
    padding-inline-start: 0;
    border-inline-start: none;
  }
}
.single-post-article__container .article-sidebar .author-info img.avatar {
  height: 104px;
}
.single-post-article__container .article-sidebar .author-info a {
  display: block;
}
.single-post-article__container .article-sidebar .author-info__name {
  font-weight: var(--fw-semibold);
}
.single-post-article__container .article-sidebar .article-share {
  display: flex;
  flex-direction: column;
  gap: var(--space-xs);
}