/* 
BORDER RADIUS UTILITY
Values for consistent border radius.
*/
:root {
    --radius-xs: 6px;
    --radius-small: 8px;
    --radius-normal: 16px;
    --radius-large: 48px;
    --radius-circle: 9999px
}

.rounded-small {
    border-radius: var(--radius-small);
}

.rounded {
    border-radius: var(--radius-normal);
}

.rounded-xs {
    border-radius: var(--radius-xs);
}